body {
	font-family: 'Roboto', sans-serif;
}


.header {
	width: 100%;
	height: 100%;
	background: url('/photos/main/main.jpg') no-repeat center center fixed;
	background-size: cover;
}

.header-top {
	background: rgba(73, 84, 91, 0.84);
	padding: 0;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 999;
	.navbar {
		margin-bottom: 10px;
	}
}

.nav-container {
	margin-top: 10px;
}

.navbar-brand {
	color: #fff;
}

.menu li a {
	color: #fff;
	text-transform: uppercase;
	font-size: 1.2em;
	font-weight: 400;
	vertical-align: middle;
	&:hover {
		text-decoration: none;
		background: transparent;
	}
	&:active {
		background: transparent;
		color: #8c8c8c
	}
}

.header-bottom {
	padding: 18% 0;
	text-align: center;
	h1, h2 {
		color: #fff;
		font-style: italic;
		font-size: 3em;
		font-weight: 700;
	}
}

.services {
	background: #43b6e5;
	padding: 6% 0;
	h2 {
		text-transform: uppercase;
		color: #fff;
		font-size: 3em;
		font-weight: 100;
		margin: 0 15px;
	}
	h3 {
		text-transform: uppercase;
		color: #fff;
		font-weight: 400;
		font-size: 1.7em;
		margin: 5% 0;
	}
	p {
		font-weight: 300;
		color: #fff;
		line-height: 1.5em;
	}
	.line {
		margin-bottom: 5%;
		position: relative;
	}
	span.bottom-line {
		width: 50px;
		display: block;
		border-bottom: 2px solid #fff;
		position: absolute;
		bottom: -10px;
		margin: 0 20px;
	}
}

































